import { Checkbox, Divider, Radio, RadioChangeEvent, message } from "antd";
import Title from "antd/es/typography/Title";
import './index.less';
import { useEffect, useState } from "react";
import { CopyOutlined } from "@ant-design/icons";
import copy from "copy-to-clipboard";
import { getAppInfo } from "@/api/modules/app";
import { useSearchParams } from "react-router-dom";
import { CheckboxChangeEvent } from "antd/es/checkbox";
import { ProCard } from "@ant-design/pro-components";

const View = () => {
    const [access, setAccess] = useState('cdn');
    const [searchParams] = useSearchParams();
    const [aid, setAid] = useState<string>('');
    const [releaseChecked, setReleaseChecked] = useState(true);
    const [useridChecked, setUseridChecked] = useState(true);
    const [envChecked, setEnvChecked] = useState(true);
    const [pvChecked, setPvChecked] = useState(true);
    const [jsChecked, setJsChecked] = useState(true);
    const [breadcrumbChecked, setBreadcrumbChecked] = useState(true);
    const [resourceChecked, setResourceChecked] = useState(true);
    const [resourceErrorChecked, setResourceErrorChecked] = useState(true);
    const [requestChecked, setRequestChecked] = useState(true);
    const [performanceChecked, setPerformanceChecked] = useState(true);
    const [blankChecked, setBlankChecked] = useState(true);
    const [configLevel, setConfigLevel] = useState('basic');

    const changeAccess = (e: RadioChangeEvent) => {
        setAccess(e.target.value)
    }

    const changeConfigLevel = (e: RadioChangeEvent) => {
        setConfigLevel(e.target.value)
    }

    const PreCode = ({ type }: any) => {
        const cdnCode = `<!-- 脚本 -->
        <!-- 页面 <head> 标签顶部添加以下代码 -->
        <script>
        !function(e,t,r,n,a){var o=function(){for(var e=[],t=0;t<arguments.length;t++)e[t]=arguments[t];e.push(Date.now(),location.href),("precollect"==e[0]?o.p.a:o.q).push(e)};o.q=[],o.p={a:[]},e[a]=o;var s=document.createElement("script");s.src=r+"?aid="+n+"&globalName="+a,s.crossOrigin="anonymous",t.getElementsByTagName("head")[0].appendChild(s),"addEventListener"in e&&(o.pcErr=function(t){var r=t||e.event,n=r.target||r.srcElement;n instanceof Element||n instanceof HTMLElement?e[a]("precollect","st",{tagName:n.tagName,url:n.getAttribute("href")||n.getAttribute("src")}):e[a]("precollect","err",r.error||r.message)},o.pcRej=function(t){var r=t||e.event;e[a]("precollect","err",r.reason||r.detail&&r.detail.reason)},e.addEventListener("error",o.pcErr,!0),e.addEventListener("unhandledrejection",o.pcRej,!0)),"PerformanceLongTaskTiming"in e&&(o.pp={entries:[]},o.pp.observer=new PerformanceObserver((function(e){o.pp.entries=o.pp.entries.concat(e.getEntries())})),o.pp.observer.observe({entryTypes:["longtask","largest-contentful-paint","layout-shift"]}))}(window,document,"https://www.unpkg.com/@owl-js/web-sdk@latest/cdn/browser.js","aid","browserClient");
        </script>
        <script>
        window.browserClient("init", {
          aid: ${aid},
          ${useridChecked ? 'userId: "请填写userId",' : ''}
          ${envChecked ? 'env: "请填写env",' : ''}
          ${releaseChecked ? 'release: "请填写release",' : ''}
          ${pvChecked || jsChecked || breadcrumbChecked || resourceChecked || resourceErrorChecked || requestChecked || performanceChecked || blankChecked ? 'plugins: {' : ''}
          ${pvChecked ? 'pageview: true,' : ''}
          ${jsChecked ? 'jsError: true,' : ''}
          ${breadcrumbChecked ? 'breadcrumb: true,' : ''}
          ${resourceChecked ? 'resource: true' : ''}
          ${resourceErrorChecked ? ' resourceError: true,' : ''}
          ${requestChecked ? 'ajax: true,fetch: true' : ''}
          ${performanceChecked ? 'performance: true' : ''}
          ${blankChecked ? ' blankScreen: true,' : ''}
          ${pvChecked || jsChecked || breadcrumbChecked || resourceChecked || resourceErrorChecked || requestChecked || performanceChecked || blankChecked ? '}' : ''}
        });
        window.browserClient("start");
        </script>`;

        const npmCode = `import browserClient from '@owl-js/web-sdk';
    
        browserClient('init', {
          aid: ${aid},
          ${useridChecked ? 'userId: "请填写userId",' : ''}
          ${envChecked ? 'env: "请填写env",' : ''}
          ${releaseChecked ? 'release: "请填写release",' : ''}
          ${pvChecked || jsChecked || breadcrumbChecked || resourceChecked || resourceErrorChecked || requestChecked || performanceChecked || blankChecked ? 'plugins: {' : ''}
          ${pvChecked ? 'pageview: true,' : ''}
          ${jsChecked ? 'jsError: true,' : ''}
          ${breadcrumbChecked ? 'breadcrumb: true,' : ''}
          ${resourceChecked ? 'resource: true' : ''}
          ${resourceErrorChecked ? ' resourceError: true,' : ''}
          ${requestChecked ? 'ajax: true,fetch: true' : ''}
          ${performanceChecked ? 'performance: true' : ''}
          ${blankChecked ? ' blankScreen: true,' : ''}
          ${pvChecked || jsChecked || breadcrumbChecked || resourceChecked || resourceErrorChecked || requestChecked || performanceChecked || blankChecked ? '}' : ''}
        });
        
        // 开启上报
        browserClient('start');`;

        const copyText = (e: React.MouseEvent, text: string) => {
            copy(text);
            message.success('已复制')
        }

        if (type === 'cdn') {
            return (<div style={{ position: 'relative' }}><CopyOutlined className="copy-btn" onClick={e => copyText(e, cdnCode)} /><pre className="code-display">{cdnCode}</pre></div>);
        }
        return (<div style={{ position: 'relative' }}><CopyOutlined className="copy-btn" onClick={e => copyText(e, npmCode)} /><pre className="code-display">{npmCode}</pre></div>);
    }

    const initAppInfo = async () => {
        const appId = searchParams.get('appId');
        if (appId) {
            const appInfo = await getAppInfo(appId);
            if (appInfo && appInfo.code === 200) {
                setAid(appInfo.data!.aid);
            }
        }
    }

    useEffect(() => {
        initAppInfo();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    const copyText = (e: React.MouseEvent, text: string) => {
        copy(text);
        message.success('已复制')
    }

    return (<ProCard
        title="项目接入"
        bordered
        headerBordered>
        <div>
            <div style={{ background: '#E8FFEA', padding: '12px 16px' }}>
                <Title level={5}>已收到上报数据</Title>
                <p>如需更改配置，请按照下面流程操作</p>
            </div>
            <div className="sdk-container">
                <div className="sdk-config">
                    <h5>1.SDK配置</h5>
                    <div style={{ marginTop: '8px' }}><span style={{ color: '#86909c' }}>AppID:</span>&nbsp;&nbsp;{aid} <CopyOutlined style={{ marginLeft: '8px' }} onClick={(e) => copyText(e, aid)} /></div>
                    <Radio.Group defaultValue={configLevel} buttonStyle="solid" style={{ marginTop: 16 }} onChange={changeConfigLevel}>
                        <Radio.Button value="basic">基础配置</Radio.Button>
                        <Radio.Button value="senior">高级配置</Radio.Button>
                    </Radio.Group>
                    <div>
                        {
                            configLevel === 'basic' ? <div className="basic-config">
                                <Checkbox checked={useridChecked} onChange={(e: CheckboxChangeEvent) => setUseridChecked(e.target.checked)}>采集用户ID，开启后将采集用户ID</Checkbox>
                                <Checkbox style={{ marginTop: '10px' }} checked={releaseChecked} onChange={(e: CheckboxChangeEvent) => setReleaseChecked(e.target.checked)}>采集版本，开启后将启用多版本能力</Checkbox>
                                <Checkbox style={{ marginTop: '10px' }} checked={envChecked} onChange={(e: CheckboxChangeEvent) => setEnvChecked(e.target.checked)}>多环境数据上报，开启后可将数据上报到对应环境</Checkbox>
                                <Checkbox style={{ marginTop: '10px' }}>SPA应用，开启后将按照SPA应用对路由进行监控</Checkbox>
                                <Title style={{ marginTop: '10px' }} level={5}>开启高级配置功能</Title>
                                <div>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={pvChecked} onChange={(e: CheckboxChangeEvent) => setPvChecked(e.target.checked)}>PV</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={jsChecked} onChange={(e: CheckboxChangeEvent) => setJsChecked(e.target.checked)}>JS错误</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={breadcrumbChecked} onChange={(e: CheckboxChangeEvent) => setBreadcrumbChecked(e.target.checked)}>面包屑</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={resourceChecked} onChange={(e: CheckboxChangeEvent) => setResourceChecked(e.target.checked)}>静态资源</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={resourceErrorChecked} onChange={(e: CheckboxChangeEvent) => setResourceErrorChecked(e.target.checked)}>静态资源错误</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={requestChecked} onChange={(e: CheckboxChangeEvent) => setRequestChecked(e.target.checked)}>请求监控</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={performanceChecked} onChange={(e: CheckboxChangeEvent) => setPerformanceChecked(e.target.checked)}>性能监控</Checkbox>
                                    <Checkbox style={{ marginLeft: '10px' }} checked={blankChecked} onChange={(e: CheckboxChangeEvent) => setBlankChecked(e.target.checked)}>白屏监控</Checkbox>
                                </div></div> : null
                        }

                    </div>
                </div>
                <Divider type="vertical" style={{ height: '100px' }}></Divider>
                <div className="code-copy">
                    <h5>2.复制探针</h5>
                    <Radio.Group defaultValue={access} buttonStyle="solid" style={{ marginTop: 16 }} onChange={changeAccess}>
                        <Radio.Button value="cdn">CDN</Radio.Button>
                        <Radio.Button value="npm">NPM包</Radio.Button>
                    </Radio.Group>
                    <PreCode type={access} style={{ marginTop: '16px' }}></PreCode>
                </div>
            </div>
        </div>
    </ProCard>);
}

export default View;